@font-face {
  font-family: 'Custom Icons';
  src: url('@{sourcesans-font-path}custom-icons.eot');
  src: url('@{sourcesans-font-path}custom-icons.eot?#iefix3') format('embedded-opentype'),
       url('@{sourcesans-font-path}custom-icons.woff') format('woff'),
       url('@{sourcesans-font-path}custom-icons.ttf') format('truetype'),
       url('@{sourcesans-font-path}custom-icons.svg#custom_icons') format('svg');
  font-weight: normal;
  font-style: normal;
}

// Catchall baseclass
.customicon {
  position: relative;
  top: 1px;
  display: inline-block;
  font-family: 'Custom Icons';
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

